//see: https://wiki.openstreetmap.org/wiki/Slippy_map_tilenames#ECMAScript_.28JavaScript.2FActionScript.2C_etc..29

function long2tile(lon, zoom) {
  return (Math.floor((lon + 180) / 360 * Math.pow(2, zoom)));
}

function lat2tile(lat, zoom) {
  return (Math.floor((1 - Math.log(Math.tan(lat * Math.PI / 180) + 1 / Math.cos(lat * Math.PI / 180)) / Math.PI) / 2 * Math.pow(2, zoom)));
}

function tile2long(x, z) {
  return (x / Math.pow(2, z) * 360 - 180);
}

function tile2lat(y, z) {
  let n = Math.PI - 2 * Math.PI * y / Math.pow(2, z);
  return (180 / Math.PI * Math.atan(0.5 * (Math.exp(n) - Math.exp(-n))));
}

export class Tile {
  constructor(z, x, y, lat, long) {
    this.z = z
    this.x = x
    this.y = y
    this.lat = lat
    this.long = long
  }

  deg2num() {
    this.x = long2tile(this.long, this.z)
    this.y = lat2tile(this.lat, this.z)
  }

  num2deg() {
    this.long = tile2long(this.x, this.z)
    this.lat = tile2lat(this.y, this.z)
  }

  osmTileUrl() {
    return `https://c.tile.openstreetmap.org/${this.z}/${this.x}/${this.y}.png`
  }

  map3TileUrl() {
    return `https://tile.map3.network/v1/api/tile/${this.z}/${this.x}/${this.y}.pbf`
    // https://120-79-153-67.tile.map3.network/
    // return `https://120-79-153-67.tile.map3.network/v1/api/tile/${this.z}/${this.x}/${this.y}.pbf`
  }
}

export class BBoxTile {
  constructor(topLeftTile, bottomRightTile) {
    this.topLeftTile = topLeftTile
    this.bottomRightTile = bottomRightTile
  }
}

export class BBoxLatLong {
  constructor(topLeftLat, topLeftLong, bottomRightLat, bottomRightLong) {
    this.topLeftLat = topLeftLat
    this.topLeftLong = topLeftLong
    this.bottomRightLat = bottomRightLat
    this.bottomRightLong = bottomRightLong
  }
}

export function newTileWithLatLong(lat, long, z) {
  let tile = new Tile(z, 0, 0, lat, long)
  tile.deg2num()
  return tile
}

export function newTileWithXY(x, y, z) {
  let tile = new Tile(z, x, y)
  tile.num2deg()
  return tile
}

export function bboxTilesFromTile(topTile, bottomTile, minZ, maxZ) {
  let tiles = []
  if (minZ > maxZ) {
    return tiles
  }

  for (let z = minZ; z <= maxZ; z++) {
    let tTopLeft = newTileWithLatLong(topTile.lat, topTile.long, z)
    let tBottomRight = newTileWithLatLong(bottomTile.lat, bottomTile.long, z)
    for (let x = tTopLeft.x; x <= tBottomRight.x; x++) {
      for (let y = tTopLeft.y; y <= tBottomRight.y; y++) {
        let tile = newTileWithXY(x, y, z)
        tiles.push(tile)
      }
    }
  }
  return tiles
}

export function bboxTilesFromCoor(topLat, topLong, bottomLat, bottomLong, minZ, maxZ) {
  let t1 = newTileWithLatLong(topLat, topLong, minZ)
  let t2 = newTileWithLatLong(bottomLat, bottomLong, minZ)
  return bboxTilesFromTile(t1, t2, minZ, maxZ)
}