import {ofType} from "redux-observable";
import * as ActionTypes from '../actions/actionTypes'
import {mergeMap, concatMap, map, delay} from "rxjs/operators";
import {Observable, of} from 'rxjs'
import {generateTestTiles, resourceTimingDuration, TileTestTask} from "../benchmark/benchmark";
import {
  calculateItemTiming,
  finishBenchmark,
  preStart,
  tickErrorItem, tickFinishItem,
  tickRunningItem
} from "../actions";

const maxLevel = 14
const requestTimesOfEachTile = 10

//see:
// https://cn.rx.js.org/class/es6/Observable.js~Observable.html#instance-method-concatMap
// https://redux-observable.js.org/docs/basics/Epics.html
export const benchmark = (actions$, state$) => {
  return actions$.pipe(
    ofType(ActionTypes.BENCHMARK),
    mergeMap(action => {
      performance.clearResourceTimings();
      performance.setResourceTimingBufferSize(5000);

      let sample = action.sample
      let tiles = generateTestTiles(sample.coordinate.latitude, sample.coordinate.longitude, maxLevel)
      let requestNum = 2 * tiles.length * requestTimesOfEachTile
      return of({what: 'setRequestNum', num: requestNum}, ...tiles, {what: 'finish'})
    }),
    concatMap(act => {
      if (act['what'] === 'finish') {
        // delay 3s to wait for calculate timing result
        return of(act).pipe(
          delay(3000)
        )
      } else if (act['what'] === 'setRequestNum') {
        return of(act)
      } else {  // tile
        return (new TileTestTask(act, requestTimesOfEachTile)).start()
      }
    }),
    map(act => {
      if (act['what'] === 'finish') {
        postResult(state$.value.process)
        return finishBenchmark(maxLevel)
        // return {type:'tt'}
      } else if (act['what'] === 'setRequestNum') {
        return preStart(act['num'])
      } else {  // is tile
        if (act['what'] === 'preRequest') {
          return tickRunningItem(act.item)
        } else if (act['what'] === 'requestFinish') {
          return calculateItemTiming(act.item)
        } else if (act['what'] === 'requestError') {
          return tickErrorItem(act.item, act.error)
        }
      }
    }),
  )
}

export const calculateTiming = actions$ => {
  return actions$.pipe(
    ofType(ActionTypes.TICK_CALCULATE_RESULT),
    delay(500),
    mergeMap(action => {
      return new Observable(emitter => {
        let retryTimes = 10
        let calculateFun = () => {
          let item = action.item
          let duration = resourceTimingDuration(item)
          if (duration === -1) {
            if (retryTimes > 0) {
              setTimeout(() => {
                calculateFun()
                retryTimes--
              }, 500)
            } else {
              // emitter.error(`Cannot find ${item.url} timing resource`)
              item['state'] = 'error'
              emitter.next(item)
              emitter.complete()
            }
          } else {
            item['duration'] = duration
            item['state'] = 'finish'
            emitter.next(item)
            emitter.complete()
          }
        }

        calculateFun()
      })
    }),
    map(item => {
      return tickFinishItem(item)
    })
  )
}

function postResult(processState) {
  let params = {
    coordinates: [],
    map3: [],
    osm: []
  }
  params.coordinates.push(processState.testingSample.coordinate.longitude)  //çº¬åº¦
  params.coordinates.push(processState.testingSample.coordinate.latitude) //ç»åº¦

  processState.testingItems.forEach(testItem => {
    if (testItem.state === 'finish') {
      params[testItem.type] && params[testItem.type].push({
        url: testItem.url,
        response_time: testItem.duration
      })
    }
  })
  fetch('https://test.tile.map3.network/tile/adanos/record', {
    method: 'POST',
    headers: new Headers({
      'Content-Type': 'application/x-www-form-urlencoded', // <-- Specifying the Content-Type
    }),
    body: `data=${JSON.stringify(params)}`
  })
    .then(res => res.json())
    .then(res => console.log(res))
}