import React, {Component} from 'react'
import {withStyles} from "@material-ui/core";
import * as PropTypes from "prop-types";
import ReactEcharts from "echarts-for-react";
import Button from "@material-ui/core/Button";
import {FormattedMessage, injectIntl} from "react-intl";

class Result extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    testAgain: PropTypes.func,
    testResult: PropTypes.object,
    testingItems: PropTypes.array,
    intl: PropTypes.object
  }

  getOption = () => {
    const {intl, testResult} = this.props
    return {
      title: {
        text: intl.formatMessage({id: 'compareResult'})
      },
      tooltip: {
        trigger: 'axis',
        formatter: function (datas) {
          let res = `<strong>Zoom Level ${datas[0].name.split('-')[1]}</strong><br/><br/>` //, val;
          if (datas[0])
            res += `map3 ${intl.formatMessage({id: 'avgTime'})}: ${datas[0].value.toFixed(1)} ms<br/>`
          if (datas[1])
            res += `map3 ${intl.formatMessage({id: 'minTime'})}: ${datas[1].value.toFixed(1)} ms<br/>`
          if (datas[2])
            res += `map3 ${intl.formatMessage({id: 'maxTime'})}: ${(datas[2].value + datas[1].value).toFixed(1)} ms<br/><br/>`

          if (datas[3])
            res += `osm ${intl.formatMessage({id: 'avgTime'})}: ${datas[3].value.toFixed(1)} ms<br/>`
          if (datas[4])
            res += `osm ${intl.formatMessage({id: 'minTime'})}: ${datas[4].value.toFixed(1)} ms<br/>`
          if (datas[5])
            res += `osm ${intl.formatMessage({id: 'maxTime'})}: ${(datas[5].value + datas[4].value).toFixed(1)} ms<br/>`
          // for (let i = 0, length = datas.length; i < length; i++) {
          //   val = (datas[i].value) + ' ms';
          //   res += datas[i].seriesName + 'ï¼š' + val + '<br/>';
          // }
          return res;
        },
      },
      legend: {
        data: ['map3', 'osm']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          boundaryGap: true,
          splitLine: {show: false},
          data: function () {
            let list = []
            for (let i = 1; i <= 14; i++) {
              list.push(`z-${i}`)
            }
            return list
          }()
        }
      ],
      yAxis: [
        {
          type: 'value',
          axisLabel: {
            formatter: '{value} ms'
          }
        },
      ],
      series: [
        {
          name: 'map3Avg',
          type: 'line',
          itemStyle: {
            color: '#269ff1',
            borderColor: '#269ff1',
          },
          smooth: true,
          symbolOffset: ['-50%', 0],
          data: function () {
            let map3Data = testResult.map3
            let list = []
            for (let i = 0; i < map3Data.length; i++) {
              list.push(map3Data[i][2])
            }
            return list
          }()
        },
        {
          name: 'map3Min',
          type: 'bar',
          stack: 'map3',
          itemStyle: {
            normal: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            },
            emphasis: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            }
          },
          data: function () {
            let map3Data = testResult.map3
            let list = []
            for (let i = 0; i < map3Data.length; i++) {
              list.push(map3Data[i][0])
            }
            return list
          }()
        },
        {
          name: 'map3',
          type: 'bar',
          stack: 'map3',
          barWidth: 2,
          itemStyle: {
            color: '#269ff1',
            borderColor: '#269ff1',
          },
          data: function () {
            let map3Data = testResult.map3
            let list = []
            for (let i = 0; i < map3Data.length; i++) {
              list.push(map3Data[i][1] - map3Data[i][0])
            }
            return list
          }()
        },
        {
          name: 'osmAvg',
          type: 'line',
          // areaStyle: {normal: {}},
          itemStyle: {
            color: '#e90046',
            borderColor: '#e90046',
          },
          smooth: true,
          symbolOffset: ['50%', 0],
          data: function () {
            let osmData = testResult.osm
            let list = []
            for (let i = 0; i < osmData.length; i++) {
              list.push(osmData[i][2])
            }
            return list
          }()
        },
        {
          name: 'osmMin',
          type: 'bar',
          stack: 'osm',
          itemStyle: {
            normal: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            },
            emphasis: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            }
          },
          data: function () {
            let osmData = testResult.osm
            let list = []
            for (let i = 0; i < osmData.length; i++) {
              list.push(osmData[i][0])
            }
            return list
          }()
        },
        {
          name: 'osm',
          type: 'bar',
          stack: 'osm',
          barWidth: 2,
          barGap: '100%',
          itemStyle: {
            color: '#e90046',
            borderColor: '#e90046',
          },
          data: function () {
            let osmData = testResult.osm
            let list = []
            for (let i = 0; i < osmData.length; i++) {
              list.push(osmData[i][1] - osmData[i][0])
            }
            return list
          }()
        },
      ]
    };
  }

  render() {
    const {classes, testAgain} = this.props;
    return (
      <div className={classes.root}>
        <ReactEcharts
          option={this.getOption()}
          style={{height: '350px', width: '90%'}}
          notMerge={false}
          className='react_for_echarts'/>

        <Button onClick={testAgain} variant="contained" size="large" color="primary"
                className={classes.btnTestAgain}>
          <FormattedMessage id='testAgain'/>
        </Button>

      </div>
    )
  }
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    paddingTop: '40px',
  },
  btnTestAgain: {
    marginTop: '40px',
  }
})

export default withStyles(styles)(injectIntl(Result))