import React, {Component} from "react";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Start from "../components/Start";
import Running from "../components/Running";
import Result from "../components/Result";
import {
  addUserLocationSample,
  benchmark,
  changeLanguage,
  finishBenchmark,
  testAgain,
  tickFinishItem
} from "../actions";

import CONSTS from "../const/consts";

import {supportedLocales} from "../locale";

import en from 'react-intl/locale-data/en'
import zh from 'react-intl/locale-data/zh'
import id from 'react-intl/locale-data/id'

import {addLocaleData, FormattedMessage, injectIntl} from "react-intl";
import FormControl from "@material-ui/core/FormControl";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import InputBase from "@material-ui/core/InputBase";

addLocaleData([...en, ...zh, ...id])

const BootstrapInput = withStyles(theme => ({
  root: {},
  input: {
    color: 'white',
    position: 'relative',
    fontSize: 16,
    width: 'auto',
    minWidth: '70px',
    textAlign: 'right',
  },
}))(InputBase);

class Main extends Component {

  state = {
    open: false,
  }

  _launch = (selectedId) => {
    // console.log(performance.getEntriesByType("resource"))
    // return
    let samples = this.props.process.sampleCoords
    let sampleMatchList = samples.filter(sample => sample.id === selectedId)
    if (sampleMatchList && sampleMatchList.length > 0) {
      let sample = sampleMatchList[0]
      this.props.benchmark(sample)
    }
  }

  _testAgain = () => {
    this.props.testAgain()
  }

  _getLocation = () => {
    navigator.geolocation.getCurrentPosition(position => {
      let userCoordinateItem = {
        id: 1,
        intlId: 'myPosition',
        coordinate: {longitude: position.coords.longitude, latitude: position.coords.latitude}
      }
      this.props.addUserLocationSample(userCoordinateItem)
    }, (error) => {
      console.log(error)
    })
  }

  componentDidMount() {
    setTimeout(() => {
      this._getLocation()
    }, 200)

    // this.observer = new PerformanceObserver((list, obj) => {
    //   let entries = list.getEntries()
    //   for (let i = 0; i < entries.length; i++) {
    //     console.log(entries[i], entries[i].name)
    //     let url = entries[i].name
    //     // let res = newResultItemByUrlAndPerformanceResTiming(url, entries[i])
    //     // if (res) {
    //     //   this.props.tickFinishItem(res)
    //     // }
    //   }
    // })
    // this.observer.observe({entryTypes: ['resource']})
  }

  // componentWillUnmount() {
  //   this.observer.disconnect()
  // }

  handleLanguageChange = event => {
    this.props.changeLanguage(event.target.value)
  }

  handleClose = () => {
    this.setState({open: false});
  };

  handleOpen = () => {
    this.setState({open: true});
  };

  render() {
    const {classes, process, locales} = this.props
    let stepView
    switch (process.state) {
      case CONSTS.PROCESS_STATE_START:
        stepView = <Start
          launch={this._launch}
          sampleCoords={process.sampleCoords}
        />
        break
      case CONSTS.PROCESS_STATE_RUNNING:
        stepView = <Running testingItems={process.testingItems}
                            testingSample={process.testingSample}
                            requestNum={process.requestNum}
        />
        break
      case CONSTS.PROCESS_STATE_FINISH:
        stepView = <Result testAgain={this._testAgain}
                           testingItems={process.testingItems}
                           testResult={process.testResult}/>
        break
      default:
        stepView = <div/>
    }

    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <img src="img/logo.svg" alt='logo' className={classes.logoImg}/>
            <Typography variant="h6" color="inherit" className={classes.grow}>
              <FormattedMessage
                id='title'
                description='title of website'
              />
            </Typography>

            <div>
              <FormControl className={classes.languageFormControl}>
                <Select
                  open={this.state.open}
                  onClose={this.handleClose}
                  onOpen={this.handleOpen}
                  value={locales.lang}
                  onChange={this.handleLanguageChange}
                  IconComponent={() => {
                    return <div/>
                  }}
                  input={<BootstrapInput name="age" id="age-customized-select"/>}
                >
                  {
                    supportedLocales.map(locale => {
                      return <MenuItem key={locale.lang}
                                       value={locale.lang}>{locale.desc}</MenuItem>
                    })
                  }
                </Select>
              </FormControl>
            </div>

            {/*<Button onClick={() => {sessionStorage.setItem('lang', 'en'); this.props.changeLanguage('en')}}>English</Button>*/}
            {/*<Button onClick={() => {sessionStorage.setItem('lang', 'zh'); this.props.changeLanguage('zh')}}>ä¸­æ–‡</Button>*/}
          </Toolbar>
        </AppBar>

        <div className={classes.content}>
          {stepView}
        </div>
      </div>
    )
  }
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    flexGrow: 1,
    height: '100vh',
    width: '100%',
  },
  content: {},
  grow: {
    flexGrow: 1,
    color: '#fff'
  },
  logoImg: {
    width: '40px',
    height: '40px',
    marginLeft: -12,
    marginRight: 20,
  },
  languageFormControl: {
    color: 'white'
  }
})

//see: https://react-redux.js.org/api/connect
export default connect(
  (state, ownProps) => ({
    userName: state.user.userName,
    userNameState: state.user.userNameState,
    process: state.process,
    locales: state.locales
  }),               //mapStateToProps
  {
    addUserLocationSample,
    benchmark,
    testAgain,
    tickFinishItem,
    finishBenchmark,
    changeLanguage
  }                //mapDispatchToProps
)(withStyles(styles)(injectIntl(Main)))