import {ofType} from "redux-observable";
import * as ActionTypes from '../actions/actionTypes'
import {catchError, map, mergeMap, takeUntil, filter} from "rxjs/operators";
import {api} from "../services/api";
import {of} from 'rxjs'

//see: https://redux-observable.js.org/
export const requestUserName = actions$ => actions$.pipe(
  ofType(ActionTypes.REQUEST_CHANGE_USER_NAME),
  mergeMap(action => {
    return api.userName().pipe(
      map(name => ({type: ActionTypes.RECEIVED_USER_NAME, payload: name.toUpperCase()})),
      takeUntil(actions$.pipe(
        filter(action => action.type === ActionTypes.CANCEL_REQUEST_USER_NAME)
      )),
      catchError(error => of({type: ActionTypes.REQUEST_USER_NAME_FAIL, payload: error.message})))
  })
)