import * as Observable from "rxjs";
import {delay, map} from "rxjs/operators";

export const api = {
  userName() {
    return Observable.from(fetch("https://www.baidu.com/", {mode: 'no-cors'})).pipe(
      delay(1000),
      map(ret => {
        const ramdom = Math.random() >= 0.495
        if (ramdom) {
          return 'Moo' + ramdom
        } else {
          throw new Error('my error')
        }
      })
    )
  }
}