import React, {Component} from 'react'
import {withStyles} from "@material-ui/core";
import * as PropTypes from "prop-types";
import CircularProgress from "@material-ui/core/CircularProgress";
import Chip from "@material-ui/core/Chip";
import {FormattedMessage, injectIntl} from "react-intl";

class Running extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    testingItems: PropTypes.array,
    testingSample: PropTypes.object,
    requestNum: PropTypes.number
  }

  scrollTop = 0

  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({behavior: "smooth"});
  }

  componentDidUpdate() {
    if (this.messagesBox.scrollHeight - this.messagesBox.clientHeight - 70 <= this.scrollTop) {
      this.scrollToBottom();
    }
  }

  onScroll = (event) => {
    this.scrollTop = event.currentTarget.scrollTop
  }

  render() {
    const {classes, testingItems, testingSample, requestNum, intl} = this.props;
    let finishNum = testingItems.length
    let progress = (finishNum / requestNum * 100).toFixed(1)

    return (
      <div className={classes.root}>
        <div className={classes.tipTitle}>
          <FormattedMessage
            id="testRunningLabel"
            values={{
              where: <font
                color={'#232323'}> [{`${intl.formatMessage({id: testingSample.intlId})} ${testingSample.coordinate.latitude},${testingSample.coordinate.longitude}`}] </font>
            }}
          />
        </div>
        <div className={classes.progressBox}>
          <CircularProgress size={100} variant={progress > 99 ? 'indeterminate' : 'static'}
                            value={parseInt(progress)}/>
          <div className={classes.progressLabel}>{progress}%</div>
        </div>
        <div className={classes.detailBox} onScroll={this.onScroll} ref={(el) => {
          this.messagesBox = el;
        }}>
          {
            testingItems.map(item => {
              let resClass = item.duration < 100 ? classes.green : classes.warn
              let stateTag = item.state === 'finish' ?
                <div
                  className={resClass}> {parseInt(item.duration)}ms</div> : (item.state === 'error' ?
                  <div className={classes.error}>error</div> :
                  <div className={classes.gray}>running...</div>)
              return (
                <div className={classes.testItem} key={item.id}>
                  <Chip className={classes.fromType}
                        label={item.type}
                        color={item.type === 'map3' ? 'primary' : 'secondary'}
                  />
                  <div style={{fontSize: '14px', color: '#999'}}>{item.url} </div>
                  {stateTag}
                </div>
              )
            })
          }
          <div ref={(el) => {
            this.messagesEnd = el;
          }}/>
        </div>
      </div>
    )
  }
}

const styles = theme => ({
  root: {},
  tipTitle: {
    margin: '16px',
    color: '#666'
  },
  progressBox: {
    position: 'relative',
    marginTop: '16px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  progressLabel: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '18px',
    fontWeight: 400,
    color: '#666',
  },
  detailBox: {
    marginTop: '32px',
    marginLeft: '16px',
    marginRight: '16px',
    padding: '8px',
    color: '#666',
    height: '300px',
    overflow: 'auto',
    backgroundColor: 'white'
  },
  testItem: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    marginTop: '8px',
  },
  fromType: {
    color: 'white',
    height: '20px',
    marginRight: '16px',
    fontSize: '12px'
  },
  green: {
    color: '#47a23a',
    marginLeft: '16px',
    fontSize: '14px'
  },
  warn: {
    color: '#f98300',
    marginLeft: '16px',
    fontSize: '14px'
  },
  error: {
    color: '#ce3733',
    marginLeft: '16px',
    fontSize: '14px'
  },
  gray: {
    color: '#666',
    marginLeft: '16px',
    fontSize: '14px'
  },
})

export default withStyles(styles)(injectIntl(Running))