import * as ActionTypes from '../actions/actionTypes'
import CONSTS from "../const/consts";

const processState = {
  state: CONSTS.PROCESS_STATE_START,
  sampleCoords: [
    {id: 2, intlId: 'BandungCityPark', coordinate: {longitude: 107.607190, latitude: -6.921487}},
    {id: 3, intlId: 'BandungShoppingCenter', coordinate: {longitude: 107.605327, latitude: -6.893337}},
    // {id: 4, intlId: 'JakartaMilitaryMuseum', coordinate: {longitude: 106.818498, latitude: -6.231728}}
    {id: 5, intlId: 'JakartaGBKS', coordinate: {longitude: 106.802769, latitude: -6.218696}}
  ],
  testingSample: {},  //æ­£åœ¨æµ‹è¯•çš„åœ°ç†æ•°æ®
  testResult: {},     //ç”¨äºŽchartsçš„æ•°æ®
  requestNum: 0,     //ä¸»è¦ç”¨äºŽè®¡ç®—å€’è®¡æ—¶ç™¾åˆ†æ¯”
  testingItems: []   //æ­£åœ¨æµ‹è¯•ä¸­çš„è¯·æ±‚items
}

export default function processReducer(state = processState, action) {
  switch (action.type) {
    case ActionTypes.ADD_USER_LOCATION_COORDINATE:
      let sampleCoords = state.sampleCoords
      let userCoordinateItem = action.userSample
      //remove if exist
      for (let i = 0; i < sampleCoords.length; i++) {
        const sample = sampleCoords[i]
        if (sample.id === userCoordinateItem.id) {
          sampleCoords.splice(i, 1)
          break
        }
        i++
      }
      sampleCoords.unshift(userCoordinateItem)
      return {...state, sampleCoords}
    case ActionTypes.BENCHMARK:
      return {
        ...state,
        state: CONSTS.PROCESS_STATE_RUNNING,
        testingSample: action.sample,
        testResult: {},
        testingItems: []
      }
    case ActionTypes.PRE_START:   //éœ€è¦åšä¸€äº›è®¡ç®—æ‰èƒ½å¾—åˆ°ä¸€äº›åˆå§‹åŒ–çš„æ•°æ®
      return {
        ...state,
        requestNum: action.requestNum
      }
    // case ActionTypes.TICK_RUNNING_ITEM:  //æ·»åŠ æ­£åœ¨è¿è¡Œitem
    //   state.testingItems.push(action.item)
    //   return {...state}
    case ActionTypes.TICK_CALCULATE_RESULT:  //when request finish, add item
      state.testingItems.push(action.item)
      return {...state}
    case ActionTypes.TICK_FINISH_ITEM:  //itemå®Œæˆtimingè®¡ç®—
      let testingItems = state.testingItems
      let id = findTestingItemIdx(testingItems, action.item)
      if (id !== -1) {
        testingItems[id] = {...testingItems[id], ...action.item}
        return {...state}
      }
      return state
    // state.testingItems.push(action.item)
    // return {...state}
    case ActionTypes.TICK_ERROR_ITEM:
      // let id2 = findTestingItemIdx(state.testingItems, action.item)
      // if (id2 !== -1) {
      //   state.testingItems[id2] = {...state.testingItems[id2], ...action.item}
      //   return {...state}
      // }
      // return state
      state.testingItems.push(action.item)
      return {...state}
    case ActionTypes.FINISH_BENCKMARK:
      let res = testingItemsForCharts(state.testingItems, action.maxLevel)
      return {...state, state: CONSTS.PROCESS_STATE_FINISH, testResult: res}
    case ActionTypes.TEST_AGAIN:
      return {...state, testResult: {}, state: CONSTS.PROCESS_STATE_START}
    default:
      return state
  }
}

function findTestingItemIdx(testingItems, searchItem) {
  for (let i = testingItems.length - 1; i >= 0; i--) {
    if (testingItems[i].id === searchItem.id) {
      return i
    }
  }
  return -1
}

function testingItemsForCharts(testingItems, maxLevel) {
  let res = {}

  let types = ['osm', 'map3']
  let levelVar = {}
  //init vars
  types.forEach(type => {
    for (let z = 1; z <= maxLevel; z++) {
      if (!levelVar[type]) {
        levelVar[type] = {}
      }
      if (!levelVar[type][z]) {
        levelVar[type][z] = {}
      }
      levelVar[type][z]['min'] = 0
      levelVar[type][z]['max'] = 0
      levelVar[type][z]['avg'] = 0
      levelVar[type][z]['sum'] = 0
      levelVar[type][z]['count'] = 0
      levelVar[type][z]['hadSet'] = false
    }
  })

  testingItems.forEach(testItem => {
    let z = parseInt(testItem.z)
    if (testItem.state === 'finish') {
      if (levelVar[testItem.type][z]['min'] === 0 && levelVar[testItem.type][z]['max'] === 0) { // first item fire
        levelVar[testItem.type][z]['min'] = testItem.duration
        levelVar[testItem.type][z]['max'] = testItem.duration
      } else {
        if (testItem.duration > levelVar[testItem.type][z]['max']) {
          levelVar[testItem.type][z]['max'] = testItem.duration
        }
        if (testItem.duration < levelVar[testItem.type][z]['min']) {
          levelVar[testItem.type][z]['min'] = testItem.duration
        }
      }

      levelVar[testItem.type][z]['sum'] += testItem.duration
      levelVar[testItem.type][z]['count'] += 1
      levelVar[testItem.type][z]['hadSet'] = true
    }
  })

  types.forEach(type => {
    res[type] = []
    for (let z = 1; z <= maxLevel; z++) {
      if (levelVar[type][z]['hadSet']) {
        let avgTime = levelVar[type][z]['sum'] / levelVar[type][z]['count']
        // let avgTime = (levelVar[type][z]['min'] + levelVar[type][z]['max']) / 2
        res[type].push([levelVar[type][z]['min'], levelVar[type][z]['max'], avgTime])
      } else {
        res[type].push([0, 0, 0])
      }
    }
  })

  return res
}