import React, {Component} from 'react'
import {withStyles} from "@material-ui/core";
import * as PropTypes from "prop-types";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import NativeSelect from "@material-ui/core/NativeSelect";
import Fab from "@material-ui/core/Fab";
import {FormattedMessage, injectIntl} from "react-intl";

class Start extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    launch: PropTypes.func,
    selectedSampleId: PropTypes.number,
    sampleCoords: PropTypes.array
  }

  state = {
    selectedSampleId: 0
  }

  _handleChange = name => event => {
    this.setState({selectedSampleId: parseInt(event.target.value)})
  }

  _launcher = () => {
    let sampleId = this.state.selectedSampleId
    if (sampleId === 0) {
      sampleId = this.props.sampleCoords[0].id
    }
    this.props.launch(parseInt(sampleId))
  }

  // componentDidMount() {
    // this.setState({selectedSampleId: this.props.selectedSampleId})
  // }

  render() {
    const {classes, sampleCoords, intl} = this.props;
    return (
      <div className={classes.root}>
        <FormControl className={classes.formControl}>
          <FormHelperText className={classes.selectLabel} component={'div'}>
            <FormattedMessage id='selectLocationLabel'/>
          </FormHelperText>
          <NativeSelect
            value={this.state.selectedSampleId}
            onChange={this._handleChange()}
            name="sampleCoordinate"
            className={classes.selectEmpty}
          >
            {
              sampleCoords.map(sample => {
                return <option key={sample.id}
                               value={sample.id}>{`${intl.formatMessage({id: sample.intlId})} ${sample.coordinate.latitude},${sample.coordinate.longitude}`}</option>
              })
            }
          </NativeSelect>
        </FormControl>
        <Fab onClick={this._launcher} color="secondary" aria-label="Add"
             className={classes.launcher}>
          <FormattedMessage id='start'/>
        </Fab>
      </div>
    )
  }
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  formControl: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    minWidth: 200,
    marginTop: '50px'
  },
  selectLabel: {
    marginRight: '20px',
    fontSize: '16px',
    color: '#666',
  },
  selectEmpty: {},
  launcher: {
    margin: theme.spacing.unit,
    width: '100px',
    height: '100px',
    color: 'white',
    marginTop: '60px',
    fontSize: '20px',
    fontWeight: 400
  }
})

export default withStyles(styles)(injectIntl(Start))