import * as ActionTypes from '../actions/actionTypes'

import {supportedLocales} from "../locale";

let lang = sessionStorage.getItem('lang')
if (!lang) {
  lang = navigator.language.split('-')[0]
  console.log(lang)
}
let selectedLocale = findLocale(lang)

const localeState = {
  ...selectedLocale
}

export default function localeReducer(state = localeState, action) {
  switch (action.type) {
    case ActionTypes.CHANGE_LANGUAGE:
      let locale = findLocale(action.lang)
      return {...locale}
    default:
      return state
  }
}

function findLocale(lang) {
  let locale = supportedLocales[0]
  for (let i = 0; i < supportedLocales.length; i++) {
    if (supportedLocales[i].lang === lang) {
      locale = supportedLocales[i]
      break
    }
  }
  return locale
}