import {newTileWithLatLong} from "../geomaths/tilemath";
import {from, range, of} from "rxjs";
import {concatMap} from "rxjs/operators";
import {hashCode} from "../util/util";

function _fetch(fetch_promise, timeout) {
  let abort_fn = null;

  let abort_promise = new Promise(function (resolve, reject) {
    abort_fn = function () {
      reject('abort promise');
    };
  });

  let abortable_promise = Promise.race([
    fetch_promise,
    abort_promise
  ]);

  setTimeout(function () {
    abort_fn();
  }, timeout);

  return abortable_promise;
}

function fetchObs(url, z) {
  // let cors = url.includes('osm_tile') ? 'cors' : 'no-cors'
  // let myHeaders = new Headers({
  //   // 'Access-Control-Allow-Origin': '*',
  //   'pragma': 'no-cache',
  //   'cache-control': 'no-cache'
  // });
  let myInit = {
    // headers: myHeaders,
    mode: 'no-cors',
    cache: "no-cache",
  }
  return from(_fetch(fetch(`${url}&t=${(new Date()).getTime()}`, myInit), 5000).then(res => {
      // console.log(res)
      let item = newResultItemByUrlAndZ(url, z)
      return {'what': 'requestFinish', item: (item || {id: hashCode(url), url})}
    }, error => {
      console.log(error)
      let item = newResultItemByUrlAndZ(url, z)
      if(item) {
        item['state'] = 'error'
      }
      return {'what': 'requestError', item: (item || {id: hashCode(url), url, state: 'error'})}
    })
  )
}

export class TileTestTask {
  constructor(tile, requestTimes = 10) {
    this.tile = tile
    this.requestTimes = requestTimes
  }

  start() {
    return range(1, this.requestTimes).pipe(
      concatMap(i => {
        return of(`${this.tile.osmTileUrl()}?osm_tile_t=${i}`, `${this.tile.map3TileUrl()}?map3_tile_t=${i}`)
      }),
      concatMap(url => {
        return from(fetchObs(url, this.tile.z))
      }),
    )
  }
}

export function resourceTimingDuration(item) {
  let url = `${item.url}?${item.type}_tile_t=${item.tick}`
  // let resources = performance.getEntriesByName(url, "resource")
  let resources = performance.getEntriesByType("resource")
  for (let i = resources.length - 1; i >= 0; i--) {
    let resource = resources[i]
    if (resource.name.includes(url)) {
      // console.log(resource)
      return getResourceDuration(resource)
    }
  }
  return -1
}

//see: https://developer.mozilla.org/zh-CN/docs/Web/API/Resource_Timing_API/Using_the_Resource_Timing_API
function getResourceDuration(resource) {
  // let redirectTime = resource.redirectEnd - resource.redirectStart;
  // let dnsTime = resource.domainLookupEnd - resource.domainLookupStart
  // let tcpTime = resource.connectEnd - resource.connectStart
  // let secureConnectTime = (resource.secureConnectionStart > 0) ? resource.connectEnd - resource.secureConnectionStart : 0
  // let fetchResponseTime = resource.responseEnd - resource.fetchStart
  return resource.responseEnd - resource.startTime
}

export function generateTestTiles(latitude, longitude, maxLevel = 14) {
  let tiles = []
  for (let level = 1; level <= maxLevel; level++) {
    let tile = newTileWithLatLong(latitude, longitude, level)
    tiles.push(tile)
  }
  return tiles
}

export function newResultItemByUrlAndZ(url, z) {
  let res = newResultItemByUrl(url)
  if (res) {
    res.z = z
  }
  return res
}

export function newResultItemByUrl(url) {
  if (url && typeof url == 'string') {
    let splitTarget = [{type: 'osm', t: '?osm_tile_t='}, {type: 'map3', t: '?map3_tile_t='}]
    for (let i = 0; i < splitTarget.length; i++) {
      let target = splitTarget[i]
      let urls = url.split(target.t)
      if (urls.length > 1) {
        return {
          id: hashCode(url),
          url: urls[0],
          tick: urls[1],
          type: target.type,
          state: 'running',
          duration: 0
        }
      }
    }
  }
  return null
}

export function newResultItemByUrlAndPerformanceResTiming(url, perfResTiming) {
  let res = newResultItemByUrl(url)
  if (res) {
    res['duration'] = perfResTiming.duration
    res['state'] = 'finish'
    return res
  }
  return null
}