import * as ActionTypes from '../actions/actionTypes'

const userState = {
  userName: 'LiLei',
  userNameState: {
    loading: false,
    errorMsg: '',
    canceled: false
  }
}

export default function userReducer(state = userState, action) {
  switch (action.type) {
    case ActionTypes.REQUEST_CHANGE_USER_NAME:
      return {...state, userNameState: {loading: true, errorMsg: ''}}
    case ActionTypes.RECEIVED_USER_NAME:
      return {...state, userName: action.payload, userNameState: {loading: false, errorMsg: ''}}
    case ActionTypes.REQUEST_USER_NAME_FAIL:
      return {...state, userNameState: {loading: false, errorMsg: action.payload}}
    case ActionTypes.CANCEL_REQUEST_USER_NAME:
      return {...state, userNameState: {canceled: true}}
    default:
      return state
  }
}