export function addUserLocationSample(userSample) {
  return {
    type: ActionTypes.ADD_USER_LOCATION_COORDINATE,
    userSample
  }
}

export function benchmark(sample) {
  return {
    type: ActionTypes.BENCHMARK,
    sample
  }
}

export function preStart(requestNum) {
  return {
    type: ActionTypes.PRE_START,
    requestNum
  }
}

