export function tickRunningItem(item) {
  return {
    type: ActionTypes.TICK_RUNNING_ITEM,
    item
  }
}

export function calculateItemTiming(item) {
  return {
    type: ActionTypes.TICK_CALCULATE_RESULT,
    item
  }
}

export function tickErrorItem(item, error) {
  return {
    type:ActionTypes.TICK_ERROR_ITEM,
    item,
    error
  }
}

export function tickFinishItem(item) {
  return {
    type: ActionTypes.TICK_FINISH_ITEM,
    item
  }
}

export function finishBenchmark(maxLevel) {
  return {
    type: ActionTypes.FINISH_BENCKMARK,
    maxLevel
  }
}

export function testAgain() {
  return {
    type: ActionTypes.TEST_AGAIN
  }
}